.contenedor {
  width: 100%;
  margin: 2rem auto;

  backdrop-filter: blur(7px) saturate(180%);
  -webkit-backdrop-filter: blur(7px) saturate(180%);
  background-color: rgba(17, 25, 40, 0.75);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
}

.detalle {
  margin: 3rem auto;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.imagenDetalle {
  width: 75%;
  margin: auto;
  align-items: center;
}

.contenido {
  width: auto;
  margin: auto;
  text-align: center;
  align-items: center;
}
.links {
  display: flex;
  flex-direction: column;
  margin-top: 10px;

  color: white;
}
.continuarCompra,
.terminarCompra {
  margin: 10px 5px 0 5px;
  padding: 0.5rem;
  background-color: #1976d2;
  color: white;
  font-weight: 700;
  border: none;
  border-radius: 10px;
  text-decoration: none;
  list-style: none;
  cursor: pointer;
  transition: all ease 0.3s;
}
.continuarCompra:hover,
.terminarCompra:hover {
  background-color: white;
  color: #1976d2;
  border: 1px solid #1976d2;
  cursor: pointer;
}
.continuarCompraLink {
  list-style: none;
}

@media screen and (min-width: 768px) {
  .contenedor {
    width: 50vh;
    align-items: center;
    justify-content: center;
  }
  .imagenDetalle {
    width: 50%;
    margin-left: auto;
  }
  .contenido {
    width: 50%;
  }
}
