.coverVideo {
  height: 100vh;
  width: 100%;
  object-fit: contain;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.video {
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: -1;
}
.coverVideo h1 {
  color: white;
  font-size: 100px;
}
.coverVideo p {
  margin-top: 8px;
  color: white;
  font-size: 32px;
}
